.modal-window-shadow {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-window-shadow.active {
  display: block;
}

.c-modal {
  padding: 91px 15px;
  max-width: 800px;
  width: 100%;
  display: none;
  transform: scale(0);
  background: #ffffff;
  max-height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
button[data-fancybox-close] {
  position: absolute;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  top: 20px;
  right: 20px;
  cursor: pointer;
  border: none;
  box-shadow: none;
  background: none;
  z-index: 99999;

  @media (max-width: 500px) {
    top: 40px;
    right: 10px;
  }
}

button[data-fancybox-close]::before,
button[data-fancybox-close]::after {
  content: '';
  position: absolute;
  display: block;
  width: 36px;
  height: 3.5px;
  background: rgb(97, 97, 97);
  top: 50%;
  left: 50%;
  transition: 0.35s;

  @media (max-width: 450px) {
    width: 28px;
    height: 2.5px;
  }

  @media (max-width: 768px) {
    width: 30px;
    height: 3px;
  }
}

button[data-fancybox-close]::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

button[data-fancybox-close]::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

button[data-fancybox-close]:hover::before,
button[data-fancybox-close]:hover::after {
  transition: 0.35s;
  background: rgb(152, 152, 152);
}
